<script>
	import { onMount } from "svelte";
	import { company } from "./db.js";
	import Draggable from "./Draggable.svelte";
	import Tailwind from "./Tailwind.svelte";

	let devMode = true;

	let flags = {
		isDMSelected: false,
		isMJBotFound: false,
		isConnectedMJ: false,
	};

	let mjActive = false,
		mjHeight = 50,
		mjConsole = [],
		mjConsoleCount = 0,
		mjBtn,
		notifications = false,
		msgsRead = [];

	let count = "";
	let startIndex = "";
	let startIndexDisplay = "";
	let url = "";
	let dataArray = []; // Array to hold generated strings
	let currentIndex = 0;
	let promptStyle = 0;
	let generationCount = 0,
		editPromptBoxDisable = true;

	let fields = [
		{ label: "Number of Companies", value: count },
		{ label: "Start Index", value: startIndexDisplay },
		{ label: "URL", value: url },
	];

	$: {
		count = fields[0].value;
		startIndex = fields[1].value;
		url = fields[2].value;
	}

	//Todo: make multiple components

	function changeStylePrompt(u) {
		styleAry.map((i) => {
			i.selected = false;
		});

		promptStyle = u.id;

		u.selected = true;

		styleAry = styleAry;

		prompt.map((i) => {
			return (i.show = false);
		});

		prompt[promptStyle].show = true;
	}

	let styleAry = [];

	function createStylePrompt(name) {
		let obj = {
			name,
			selected: styleAry.length == 0 ? true : false,
			id: styleAry.length,
		};
		styleAry = [...styleAry, obj];
	}

	createStylePrompt("Original");
	createStylePrompt("Pattern");

	let prompt = [
		{
			name: "style1",
			str: `|url| |colours|, Create a detailed and surreal micrograph image showcasing an abstract interpretation of a virus cell. The cell should be rendered in saturated, set against a monochrome complementary background that enhances its visibility. The virus cell is to be centered and the sole focus, with no other objects present, isolated object. highlighted under a single, centralized light source from above to create dramatic shadows and emphasize texture. The surface of the virus should display extreme textural variety, suggesting a complex and intricate biological structure. Show the Flagellum around the cell, Aim for a hyper-realistic aesthetic --iw 0.5 --style tDJNC9i1IkHpbPfeFGrfPjh  --no |companyNumber|`,
			show: true,
		},
		{
			name: "style2",
			str: `|url|, |colours|, Create a highly detailed and realistic micrograph representation of endothelial cells, emphasizing their role in cardiovascular research.in a manner that maintains the cells' natural appearance. Focus on the intricate network of these cells, highlighting their interconnectivity and the delicate balance they maintain in vascular health. The image should be from a top-down perspective, providing a comprehensive view of the endothelial cell structure. Aim for a scientifically accurate portrayal, showcasing the cells in their native environment, possibly within a blood vessel. The overall aesthetic should be elegant and sophisticated, with a clear emphasis on the cells' role in medical science.--no text, logos, black. --style h4HXB7XI66EvcGSvfCK7nSTh --tile --no |companyNumber|`,
			show: false,
		},
	];

	let specificCompanies = false;

	function generateStrings() {
		let dataArray = [];
		let companyArray;
		let isSpecificCompanies = count.includes(",");

		if (isSpecificCompanies) {
			companyArray = count.split(",").map(Number);
			fields[1].label = "Total Amount of Companies";
			specificCompanies = true;
			startIndexDisplay = companyArray.length.toString(); // Update display
		} else {
			companyArray = Array.from(
				{ length: parseInt(count) },
				(_, i) => i + parseInt(startIndex),
			);
			fields[1].label = "Start Index";
			specificCompanies = false;
			startIndexDisplay = startIndex.toString(); // Update display
		}

		companyArray.forEach((index) => {
			let item = company[index];
			if (!item) return; // Skip if item doesn't exist

			let str = prompt[promptStyle].str;
			str = str
				.replace("|url|", url)
				.replace("|colours|", item.colour)
				.replace("|companyNumber|", index);

			dataArray.push(str);
		});

		console.log(dataArray);

		compareAndUpdate();

		if (dataArray < 1) {
			if (checkMsg("generateCount")) {
				createMsg(
					"generateCount",
					"fail",
					"Failed to generate, please fill out number of companies",
				);
			} else {
				editMsg(
					"generateCount",
					"fail",
					"Again failed to generate, please fill out number of companies",
				);
			}
		} else {
			createMsg(
				`generation-${generationCount}`,
				"success",
				`Generated prompts`,
			);
			copyToClipboard(dataArray);

			console.log(dataArray);
		}
	}

	// Function to compare the text content
	function compareAndUpdate() {
		const scroller = document.querySelector(".scrollerInner__059a5");
		if (!scroller) {
			console.error("Scroller not found");
			return;
		}

		// Method 1: Using Array Conversion
		const messages = Array.from(
			scroller.querySelectorAll(".messageListItem__6a4fb"),
		);
		const lastMessage = messages[messages.length - 1];
	}

	// Function to copy text to clipboard
	function copyToClipboard(array) {
		// Convert array to a string that looks like JavaScript code
		const text =
			"[" +
			array.map((item) => `"${item.replace(/'/g, "\\'")}"`).join(", ") +
			"]";

		navigator.clipboard.writeText(text).then(
			function () {
				console.log("Copying to clipboard was successful!");
			},
			function (err) {
				console.error("Could not copy text: ", err);
			},
		);
	}

	// Event listener for Enter key

	function createMsg(name, status, text, dev) {
		let obj = {
			name,
			status,
			text,
			dev,
			id: mjConsoleCount++,
		};

		mjConsole.push(obj);
		mjConsole = mjConsole;
	}

	function editMsg(name, status, text) {
		mjConsole.map((i) => {
			if (i.name == name) {
				i.status = status;
				i.text = text;
			}
		});

		mjConsole = mjConsole;
	}

	function checkMsg(name) {
		let msg = mjConsole.filter((i) => {
			return i.name == name;
		});

		return msg.length > 0 ? true : false;
	}

	// function mjCheckUp() {
	// 	if (devMode) {
	// 		mjHeight = 200;
	// 		mjExtend = true;
	// 	}

	// 	if (!checkMsg("checkDM")) {
	// 		createMsg("checkDM", "success", "DMs is currently selected", true);
	// 	}

	// 	//select DM btn
	// 	dmElement = document.querySelector(".tutorialContainer__890ea");

	// 	// Check if the DM section is active
	// 	if (dmElement && dmElement.querySelector(".selected_ad2d13")) {
	// 		flags.isDMSelected = true;
	// 		editMsg("checkDM", "success", "DMs is currently selected");
	// 	} else {
	// 		flags.isDMSelected = false;
	// 		editMsg("checkDM", "fail", "DMs is currently not selected");
	// 		return;
	// 	}
	// }

	let clickListener = function (event) {
		setTimeout(() => {
			if (mjActive) {
				quickCheck();
			}
		}, 500);
	};

	function setupClickListeners() {
		const addOrRemove = mjActive ? "addEventListener" : "removeEventListener";
		const scrollerElement = document.querySelector(".scroller__3d071");
		const privateChannelsElement = document.querySelector(".sidebar_ded4b5");

		scrollerElement?.[addOrRemove]("click", clickListener);
		privateChannelsElement?.[addOrRemove]("click", clickListener);
	}

	function dmSelected() {
		let currentURL = window.location.href;

		function check() {
			return currentURL.includes("@me");
		}

		flags.isDMSelected = check();
	}

	function MJBotFound() {
		const dmChannel = document.querySelector(".scroller__4b984");
		if (!dmChannel) {
			flags.isMJBotFound = false;
			return;
		}

		let list = dmChannel.querySelectorAll(".channel_c21703");
		flags.isMJBotFound = false; // Reset flag before checking

		for (let i of list) {
			let name = i.querySelector(".overflow__87fe8");
			if (name && name.innerText === "Midjourney Bot") {
				mjBtn = name.closest("a");
				flags.isMJBotFound = true;
				break; // Exit the loop once found
			}
		}
	}

	function connectedMJ() {
		console.log(mjBtn);
		let parentClass = mjBtn.parentElement.getAttribute("class");

		if (parentClass.includes("selected_d94cf9")) {
			flags.isConnectedMJ = true;
		} else {
			flags.isConnectedMJ = false;
		}
	}

	function quickCheck() {
		dmSelected();
		if (!flags.isDMSelected) {
			flags.isMJBotFound = false;
			flags.isConnectedMJ = false;
		}
		flags.isDMSelected ? MJBotFound() : false;
		flags.isMJBotFound ? connectedMJ() : false;

		if (flags.isConnectedMJ) {
			mjConnect();
		} else {
			if (checkMsg("connected")) {
				setTimeout(() => {
					editMsg(
						"connected",
						"disconnected",
						"Disconnected to Midjourney Bot",
					);
				}, 500);
			}
		}
	}

	function midjourneyApp() {
		mjActive = !mjActive;
		setupClickListeners();
		quickCheck();
	}
	//btn to initiate connecting
	function mjConnect() {
		if (!flags.isDMSelected) {
			document.querySelector(".wrapper_d281dd").click();
			quickCheck();
			setTimeout(() => {
				mjConnect();
			}, 200);
		} else if (!flags.isMJBotFound) {
			if (checkMsg("mjNotFound") == false) {
				createMsg(
					"mjNotFound",
					"fail",
					"Could not find Midjourney Bot in friend list, please go to Midjourney's server and message the bot",
				);
			} else {
				editMsg(
					"mjNotFound",
					"fail",
					"As mentioned before we could not find Midjourney Bot in friend list, please go to Midjourney's server and message the bot",
				);
			}

			return;
		} else if (!flags.isConnectedMJ) {
			if (checkMsg("connected")) {
				setTimeout(() => {
					editMsg("connected", "success", "Connected to Midjourney Bot");
					mjBtn.click();
				}, 500);
			} else {
				setTimeout(() => {
					createMsg("connected", "loading", "Looking for Midjourney Bot");
					mjConnect();
				});
			}
		}
	}

	let anUpdate = false;

	// Function for deep cloning an array of objects
	function deepClone(array) {
		return array.map((item) => JSON.parse(JSON.stringify(item)));
	}

	function updateMsgsRead() {
		msgsRead = deepClone(mjConsole);
		anUpdate = false;
	}

	function checkForUpdates() {
		console.log(msgsRead, mjConsole);
		if (
			msgsRead.length < mjConsole.length ||
			JSON.stringify(msgsRead) !== JSON.stringify(mjConsole)
		) {
			if (!notifications) {
				anUpdate = true;
			} else {
				updateMsgsRead();
			}
		}
	}

	$: {
		if (notifications) {
			updateMsgsRead();
		} else {
			checkForUpdates();
		}
	}

	$: mjConsole, checkForUpdates();

	let reversedMjConsole;
	$: reversedMjConsole = [...mjConsole].reverse();

	//the current selected dm in friendlist
	//selected_d94cf9

	//discord directmsg
	//tutorialContainer__890ea -parent
	//selected_ad2d13 -child
	let mjConnectIconHover = false,
		mjConnectToIconHover = false,
		notificationHover = false;

	onMount(() => {
		setTimeout(() => {
			function addingMarginRight(i) {
				function apply(u) {
					u.style.width = "calc(100% - 50px)";
				}
				if (Array.isArray(i)) {
					for (let u of i) {
						apply(u);
					}
				} else {
					apply(i);
				}
			}

			//creating a sidebar
			addingMarginRight([document.body, document.querySelector("#app-mount")]);

			setupClickListeners();

			//we'll also need to keep track if servers/channels or friends are pressed so we can close the menu
			//a lot to consider
			//if none of the above work make a sidebar on the right that pushes discords body
			//if you do this you'll need to not only add this width: calc(100% - 50px); to body but also on appmount
		}, 5000);
	});
</script>

