							<button
								on:click={() => {
									editPromptBoxDisable = !editPromptBoxDisable;
								}}
								class="px-2 py-1 font-thin text-white text-sm transition-all duration-200 bg-blue-500 hover:bg-blue-600 w-[100px]"
								>Edit prompt</button
							>
						</div>

						<!--each input-->
						{#each fields as field}
							<div class="flex mb-3">
								<p class="flex-1 mr-2 my-0 text-sm text-white">
									{field.label}:
								</p>
								<input
									type="text"
									bind:value={field.value}
									class="{field.label == 'URL'
										? 'w-[200px]'
										: 'w-[50px]'} bg-transparent text-white border-b-[1px] border-zinc-700 hover:border-white transition-all duration-200 focus:border-white"
									disabled={specificCompanies &&
										field.label == "Total Amount of Companies"}
								/>
								<p class="text-white">
									{specificCompanies &&
									field.label == "Total Amount of Companies"
										? startIndexDisplay
										: ""}
								</p>
							</div>
						{/each}

						<button
							on:click={() => {
								generateStrings();
							}}
							class="bg-blue-500 text-white mb-1 transition-all duration-200 hover:bg-blue-700 rounded-none p-2"
							>Generate</button
						>
					</div>
				{:else}
					<button
						on:mouseenter={() => {
							mjConnectToIconHover = true;
						}}
						on:mouseleave={() => {
							mjConnectToIconHover = false;
						}}
						on:click={() => {
							mjConnect();
						}}
						class="h-12 w-12 bg-transparent fill-current text-gray-300 flex justify-center items-center"
					>
