<Tailwind />

<style>
	.mjlogo {
		border-right: 1px solid transparent; /* Set the width and make the base border transparent */
		border-image: linear-gradient(
				180deg,
				rgba(65, 65, 65, 0) 0%,
				rgba(180, 183, 205, 1) 19%,
				rgba(217, 217, 224, 1) 50%,
				rgba(180, 183, 205, 1) 81%,
				rgba(88, 99, 194, 0) 100%
			)
			1 100%;
	}

	.mjbtns button path {
		transition: all 0.2s ease;
	}

	::-webkit-scrollbar {
		width: 18px;
	}

	::-webkit-scrollbar-track {
		background-color: transparent;
	}

	::-webkit-scrollbar-thumb {
		background-color: #c4b4ac;
		border-radius: 20px;
		border: 6px solid transparent;
		background-clip: content-box;
		transition: background-color 0.2s ease;
	}

	::-webkit-scrollbar-thumb:hover {
		background-color: #e1d9d6;
	}
</style>
