<script>
  export let left = 100;
  export let top = 100,
    width,
    height,
    active;

  let moving = false;

  function onMouseDown(event) {
    if (!isInput(event.target)) {
      moving = true;
    }
  }

  function onMouseMove(e) {
    if (moving) {
      left += e.movementX;
      top += e.movementY;
    }
  }

  function onMouseUp() {
    moving = false;
  }

  function isInput(element) {
    while (element) {
      if (element.tagName === "INPUT" || element.tagName === "TEXTAREA") {
        return true;
      }
      element = element.parentElement;
    }
    return false;
  }
</script>

<section
  style="left: {left}px; top: {top}px; height:{height}px; width: {width}px;"
  class="draggable"
  class:active
  on:mousedown={onMouseDown}
>
  <slot />
</section>

<svelte:window on:mouseup={onMouseUp} on:mousemove={onMouseMove} />

<style>
  .draggable {
    user-select: none;
    position: fixed;
    display: none;
    z-index: 9999999;
    border-radius: 10px;
    transition: height 0.2s ease;
    flex-direction: column;
  }

  .draggable.active {
    display: flex;
  }
</style>
